<script>
import { LabeledInput } from '@rancher/components';
import LabeledSelect from '@shell/components/form/LabeledSelect';
import ArrayList from '@shell/components/form/ArrayList';
import { get, set } from '@shell/utils/object';

export default {
  name: 'VMDiskConfig',
  components: {
    LabeledInput,
    LabeledSelect,
    ArrayList,
  },
  
  props: {
    value: {
      type: Object,
      required: true,
    },
    mode: {
      type: String,
      required: true,
    },
  },

  data() {
    return {
      busTypes: [
        { label: 'virtio', value: 'virtio' },
        { label: 'sata', value: 'sata' },
        { label: 'scsi', value: 'scsi' },
      ],
      volumeTypes: [
        { label: 'PVC', value: 'pvc' },
        { label: 'ConfigMap', value: 'configMap' },
        { label: 'Secret', value: 'secret' },
        { label: 'EmptyDir', value: 'emptyDir' },
        { label: 'Cloud-Init', value: 'cloudInit' },
      ],
    };
  },

  computed: {
    isView() {
      return this.mode === 'view';
    },

    disks: {
      get() {
        return get(this.value, 'spec.template.spec.domain.devices.disks') || [];
      },
      set(newValue) {
        set(this.value, 'spec.template.spec.domain.devices.disks', newValue);
      },
    },

    volumes: {
      get() {
        return get(this.value, 'spec.template.spec.volumes') || [];
      },
      set(newValue) {
        set(this.value, 'spec.template.spec.volumes', newValue);
      },
    },

    diskVolumeConfigs: {
      get() {
        const disks = this.disks;
        const volumes = this.volumes;
        
        return disks.map(disk => {
          const volume = volumes.find(v => v.name === disk.name) || {};
          return {
            name: disk.name || '',
            bus: disk.disk?.bus || 'virtio',
            volumeType: this.getVolumeType(volume),
            pvcName: volume.persistentVolumeClaim?.claimName || '',
            size: volume.emptyDir?.sizeLimit || '',
          };
        });
      },
      set(newValue) {
        const disks = [];
        const volumes = [];

        newValue.forEach(config => {
          if (config.name) {
            // Add disk configuration
            disks.push({
              name: config.name,
              disk: {
                bus: config.bus || 'virtio'
              }
            });

            // Add volume configuration
            const volume = { name: config.name };
            
            switch (config.volumeType) {
              case 'pvc':
                if (config.pvcName) {
                  volume.persistentVolumeClaim = { claimName: config.pvcName };
                }
                break;
              case 'emptyDir':
                volume.emptyDir = {};
                if (config.size) {
                  volume.emptyDir.sizeLimit = config.size;
                }
                break;
              case 'cloudInit':
                volume.cloudInitNoCloud = {
                  userData: config.userData || '#cloud-config\n'
                };
                break;
            }

            volumes.push(volume);
          }
        });

        this.disks = disks;
        this.volumes = volumes;
      },
    },
  },

  methods: {
    getVolumeType(volume) {
      if (volume.persistentVolumeClaim) return 'pvc';
      if (volume.configMap) return 'configMap';
      if (volume.secret) return 'secret';
      if (volume.emptyDir) return 'emptyDir';
      if (volume.cloudInitNoCloud) return 'cloudInit';
      return 'pvc';
    },

    addDisk() {
      const configs = [...this.diskVolumeConfigs];
      configs.push({
        name: '',
        bus: 'virtio',
        volumeType: 'pvc',
        pvcName: '',
        size: '',
      });
      this.diskVolumeConfigs = configs;
    },

    removeDisk(index) {
      const config = [...this.diskVolumeConfigs];
      config.splice(index, 1);
      this.diskVolumeConfigs = config;
    },
  },
};
</script>

<template>
  <div>
    <div class="row mb-20">
      <div class="col span-12">
        <h3>{{ t('kubevirt.edit.vm.fields.disks.title') }}</h3>
        <p class="text-muted">{{ t('kubevirt.edit.vm.fields.disks.description') }}</p>
      </div>
    </div>

    <div v-for="(disk, index) in diskVolumeConfigs" :key="`disk-${index}`" class="disk-config mb-20">
      <div class="row mb-10">
        <div class="col span-12">
          <h4>{{ t('kubevirt.edit.vm.fields.disks.diskTitle', { index: index + 1 }) }}</h4>
        </div>
      </div>
      
      <div class="row mb-10">
        <div class="col span-4">
          <LabeledInput
            v-model:value="disk.name"
            :mode="mode"
            :label="t('kubevirt.edit.vm.fields.disks.name.label')"
            :placeholder="t('kubevirt.edit.vm.fields.disks.name.placeholder')"
          />
        </div>
        <div class="col span-4">
          <LabeledSelect
            v-model:value="disk.bus"
            :mode="mode"
            :options="busTypes"
            :label="t('kubevirt.edit.vm.fields.disks.bus.label')"
          />
        </div>
        <div class="col span-4">
          <LabeledSelect
            v-model:value="disk.volumeType"
            :mode="mode"
            :options="volumeTypes"
            :label="t('kubevirt.edit.vm.fields.disks.volumeType.label')"
          />
        </div>
      </div>

      <div v-if="disk.volumeType === 'pvc'" class="row mb-10">
        <div class="col span-6">
          <LabeledInput
            v-model:value="disk.pvcName"
            :mode="mode"
            :label="t('kubevirt.edit.vm.fields.disks.pvcName.label')"
            :placeholder="t('kubevirt.edit.vm.fields.disks.pvcName.placeholder')"
          />
        </div>
      </div>

      <div v-if="disk.volumeType === 'emptyDir'" class="row mb-10">
        <div class="col span-6">
          <LabeledInput
            v-model:value="disk.size"
            :mode="mode"
            :label="t('kubevirt.edit.vm.fields.disks.size.label')"
            :placeholder="t('kubevirt.edit.vm.fields.disks.size.placeholder')"
          />
        </div>
      </div>

      <div class="row mb-10">
        <div class="col span-12">
          <button
            v-if="!isView"
            type="button"
            class="btn btn-sm role-link"
            @click="removeDisk(index)"
          >
            {{ t('kubevirt.edit.vm.fields.disks.remove') }}
          </button>
        </div>
      </div>

      <hr v-if="index < diskVolumeConfigs.length - 1" class="section-divider">
    </div>

    <div class="row">
      <div class="col span-12">
        <button
          v-if="!isView"
          type="button"
          class="btn btn-sm role-tertiary"
          @click="addDisk"
        >
          {{ t('kubevirt.edit.vm.fields.disks.add') }}
        </button>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
