  margin-bottom: 10px;
}

.text-muted {
  color: var(--muted);
  font-size: 0.9em;
  margin-bottom: 15px;
}

.disk-config {
  border: 1px solid var(--border);
  border-radius: 4px;
  padding: 15px;
  background-color: var(--box-bg);
}

.section-divider {
  margin: 20px 0;
  border: none;
  border-top: 1px solid var(--border);
}
</style>