<script>
import { LabeledInput } from '@rancher/components';
import YamlEditor, { EDITOR_MODES } from '@shell/components/YamlEditor';
import { get, set } from '@shell/utils/object';

export default {
  name: 'VMCloudInitConfig',
  components: {
    LabeledInput,
    YamlEditor,
  },
  
  props: {
    value: {
      type: Object,
      required: true,
    },
    mode: {
      type: String,
      required: true,
    },
  },

  data() {
    return {
      defaultCloudInitData: `#cloud-config
hostname: vm-01
users:
  - name: cloud-user
    sudo: ALL=(ALL) NOPASSWD:ALL
    ssh_authorized_keys:
      - ssh-rsa AAAAB3NzaC1yc2EAAAADAQABAAABAQC7...
packages:
  - qemu-guest-agent
runcmd:
  - systemctl enable qemu-guest-agent
  - systemctl start qemu-guest-agent`,
    };
  },

  computed: {
    editorMode() {
      return this.isView ? EDITOR_MODES.VIEW_CODE : EDITOR_MODES.EDIT_CODE;
    },

    isView() {
      return this.mode === 'view';
    },

    cloudInitUserData: {
      get() {
        const volumes = get(this.value, 'spec.template.spec.volumes') || [];
        const cloudInitVolume = volumes.find(v => v.cloudInitNoCloud);
        return cloudInitVolume?.cloudInitNoCloud?.userData || '';
      },
      set(newValue) {
        const volumes = get(this.value, 'spec.template.spec.volumes') || [];
        let cloudInitVolume = volumes.find(v => v.cloudInitNoCloud);
        
        if (!cloudInitVolume) {
          // Create new cloud-init volume
          cloudInitVolume = {
            name: 'cloudinitdisk',
            cloudInitNoCloud: {}
          };
          volumes.push(cloudInitVolume);
          
          // Also add corresponding disk
          const disks = get(this.value, 'spec.template.spec.domain.devices.disks') || [];
          disks.push({
            name: 'cloudinitdisk',
            disk: {
              bus: 'virtio'
            }
          });
          set(this.value, 'spec.template.spec.domain.devices.disks', disks);
        }

        cloudInitVolume.cloudInitNoCloud.userData = newValue;
        set(this.value, 'spec.template.spec.volumes', volumes);
      },
    },

    cloudInitNetworkData: {
      get() {
        const volumes = get(this.value, 'spec.template.spec.volumes') || [];
        const cloudInitVolume = volumes.find(v => v.cloudInitNoCloud);
        return cloudInitVolume?.cloudInitNoCloud?.networkData || '';
      },
      set(newValue) {
        const volumes = get(this.value, 'spec.template.spec.volumes') || [];
        let cloudInitVolume = volumes.find(v => v.cloudInitNoCloud);
        
        if (!cloudInitVolume) {
          cloudInitVolume = {
            name: 'cloudinitdisk',
            cloudInitNoCloud: {}
          };
          volumes.push(cloudInitVolume);
        }

        if (newValue) {
          cloudInitVolume.cloudInitNoCloud.networkData = newValue;
        } else {
          delete cloudInitVolume.cloudInitNoCloud.networkData;
        }
        
        set(this.value, 'spec.template.spec.volumes', volumes);
      },
    },

    hasCloudInit() {
      const volumes = get(this.value, 'spec.template.spec.volumes') || [];
      return volumes.some(v => v.cloudInitNoCloud);
    },
  },

  methods: {
    enableCloudInit() {
      if (!this.hasCloudInit) {
        this.cloudInitUserData = this.defaultCloudInitData;
      }
    },

    disableCloudInit() {
      const volumes = get(this.value, 'spec.template.spec.volumes') || [];
      const filteredVolumes = volumes.filter(v => !v.cloudInitNoCloud);
      set(this.value, 'spec.template.spec.volumes', filteredVolumes);

      // Also remove corresponding disk
      const disks = get(this.value, 'spec.template.spec.domain.devices.disks') || [];
      const filteredDisks = disks.filter(d => d.name !== 'cloudinitdisk');
      set(this.value, 'spec.template.spec.domain.devices.disks', filteredDisks);
    },

    insertTemplate(template) {
      switch (template) {
        case 'basic':
          this.cloudInitUserData = this.defaultCloudInitData;
          break;
        case 'ubuntu':
          this.cloudInitUserData = `#cloud-config
hostname: ubuntu-vm
users:
  - name: ubuntu
    sudo: ALL=(ALL) NOPASSWD:ALL
    shell: /bin/bash
    ssh_authorized_keys:
      - ssh-rsa AAAAB3NzaC1yc2EAAAADAQABAAABAQC7...
packages:
  - qemu-guest-agent
  - curl
  - wget
package_update: true
package_upgrade: true
runcmd:
  - systemctl enable qemu-guest-agent
  - systemctl start qemu-guest-agent`;
          break;
        case 'centos':
          this.cloudInitUserData = `#cloud-config
hostname: centos-vm
users:
  - name: centos
    sudo: ALL=(ALL) NOPASSWD:ALL
    shell: /bin/bash
    ssh_authorized_keys:
      - ssh-rsa AAAAB3NzaC1yc2EAAAADAQABAAABAQC7...
packages:
  - qemu-guest-agent
yum_repos:
  epel:
    name: EPEL
    baseurl: https://download.fedoraproject.org/pub/epel/\$releasever/\$basearch/
    enabled: true
    gpgcheck: true
    gpgkey: https://download.fedoraproject.org/pub/epel/RPM-GPG-KEY-EPEL-\$releasever
runcmd:
  - systemctl enable qemu-guest-agent
  - systemctl start qemu-guest-agent`;
          break;
      }
    },
  },
};
</script>

<template>
  <div>
    <div class="row mb-20">
      <div class="col span-12">
        <h3>{{ t('kubevirt.edit.vm.fields.cloudinit.title') }}</h3>
        <p class="text-muted">{{ t('kubevirt.edit.vm.fields.cloudinit.description') }}</p>
      </div>
    </div>

    <div class="row mb-20">
      <div class="col span-12">
        <div class="cloud-init-controls">
          <button
            v-if="!hasCloudInit && !isView"
            type="button"
            class="btn btn-sm role-secondary"
            @click="enableCloudInit"
          >
            {{ t('kubevirt.edit.vm.fields.cloudinit.enable') }}
          </button>
          
          <button
            v-if="hasCloudInit && !isView"
            type="button"
            class="btn btn-sm role-link"
            @click="disableCloudInit"
          >
            {{ t('kubevirt.edit.vm.fields.cloudinit.disable') }}
          </button>

          <div v-if="hasCloudInit && !isView" class="template-buttons ml-10">
            <span class="text-muted mr-5">{{ t('kubevirt.edit.vm.fields.cloudinit.templates') }}:</span>
            <button
              type="button"
              class="btn btn-sm role-tertiary mr-5"
              @click="insertTemplate('basic')"
            >
              {{ t('kubevirt.edit.vm.fields.cloudinit.templateOptions.basic') }}
            </button>
            <button
              type="button"
              class="btn btn-sm role-tertiary mr-5"
              @click="insertTemplate('ubuntu')"
            >
              {{ t('kubevirt.edit.vm.fields.cloudinit.templateOptions.ubuntu') }}
            </button>
            <button
              type="button"
              class="btn btn-sm role-tertiary"
              @click="insertTemplate('centos')"
            >
              {{ t('kubevirt.edit.vm.fields.cloudinit.templateOptions.centos') }}
            </button>
          </div>
        </div>
      </div>
    </div>

    <div v-if="hasCloudInit">
      <div class="row mb-20">
        <div class="col span-12">
          <h4>{{ t('kubevirt.edit.vm.fields.cloudinit.userData.label') }}</h4>
          <p class="text-muted mb-10">{{ t('kubevirt.edit.vm.fields.cloudinit.userData.description') }}</p>
          <YamlEditor
            ref="userDataEditorRef"
            v-model:value="cloudInitUserData"
            :editor-mode="editorMode"
            class="yaml-editor"
            :min-height="200"
          />
        </div>
      </div>

      <div class="row mb-20">
        <div class="col span-12">
          <h4>{{ t('kubevirt.edit.vm.fields.cloudinit.networkData.label') }}</h4>
          <p class="text-muted mb-10">{{ t('kubevirt.edit.vm.fields.cloudinit.networkData.description') }}</p>
          <YamlEditor
            ref="networkDataEditorRef"
            v-model:value="cloudInitNetworkData"
            :editor-mode="editorMode"
            class="yaml-editor"
            :min-height="100"
          />
        </div>
      </div>
    </div>

    <div v-else class="empty-state">
      <div class="empty-state-content">
        <h4>{{ t('kubevirt.edit.vm.fields.cloudinit.disabled.title') }}</h4>
        <p>{{ t('kubevirt.edit.vm.fields.cloudinit.disabled.description') }}</p>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
h3, h4 {
  margin-bottom: 10px;
}

.text-muted {
  color: var(--muted);
  font-size: 0.9em;
}

.cloud-init-controls {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
}

.template-buttons {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.yaml-editor {
  border: 1px solid var(--border);
  border-radius: 4px;
}

.empty-state {
  text-align: center;
  padding: 40px;
  border: 2px dashed var(--border);
  border-radius: 4px;
  background-color: var(--box-bg);
}

.empty-state-content {
  h4 {
    color: var(--muted);
    margin-bottom: 10px;
  }
  
  p {
    color: var(--muted);
    font-size: 0.9em;
  }
}
</style>