<script>
import { LabeledInput } from '@rancher/components';
import LabeledSelect from '@shell/components/form/LabeledSelect';
import { get, set } from '@shell/utils/object';

export default {
  name: 'VMNetworkConfig',
  components: {
    LabeledInput,
    LabeledSelect,
  },
  
  props: {
    value: {
      type: Object,
      required: true,
    },
    mode: {
      type: String,
      required: true,
    },
  },

  data() {
    return {
      interfaceTypes: [
        { label: 'masquerade', value: 'masquerade' },
        { label: 'bridge', value: 'bridge' },
        { label: 'sriov', value: 'sriov' },
      ],
      networkTypes: [
        { label: 'Pod Network', value: 'pod' },
        { label: 'Multus', value: 'multus' },
      ],
    };
  },

  computed: {
    isView() {
      return this.mode === 'view';
    },

    networks: {
      get() {
        return get(this.value, 'spec.template.spec.networks') || [];
      },
      set(newValue) {
        set(this.value, 'spec.template.spec.networks', newValue);
      },
    },

    interfaces: {
      get() {
        return get(this.value, 'spec.template.spec.domain.devices.interfaces') || [];
      },
      set(newValue) {
        set(this.value, 'spec.template.spec.domain.devices.interfaces', newValue);
      },
    },

    networkConfigs: {
      get() {
        const networks = this.networks;
        const interfaces = this.interfaces;
        
        return networks.map(network => {
          const iface = interfaces.find(i => i.name === network.name) || {};
          return {
            name: network.name || '',
            networkType: this.getNetworkType(network),
            networkName: network.multus?.networkName || '',
            interfaceType: this.getInterfaceType(iface),
            macAddress: iface.macAddress || '',
          };
        });
      },
      set(newValue) {
        const networks = [];
        const interfaces = [];

        newValue.forEach(config => {
          if (config.name) {
            // Add network configuration
            const network = { name: config.name };
            
            switch (config.networkType) {
              case 'pod':
                network.pod = {};
                break;
              case 'multus':
                if (config.networkName) {
                  network.multus = { networkName: config.networkName };
                }
                break;
            }

            networks.push(network);

            // Add interface configuration
            const iface = { name: config.name };
            
            switch (config.interfaceType) {
              case 'masquerade':
                iface.masquerade = {};
                break;
              case 'bridge':
                iface.bridge = {};
                break;
              case 'sriov':
                iface.sriov = {};
                break;
            }

            if (config.macAddress) {
              iface.macAddress = config.macAddress;
            }

            interfaces.push(iface);
          }
        });

        this.networks = networks;
        this.interfaces = interfaces;
      },
    },
  },

  methods: {
    getNetworkType(network) {
      if (network.pod) return 'pod';
      if (network.multus) return 'multus';
      return 'pod';
    },

    getInterfaceType(iface) {
      if (iface.masquerade) return 'masquerade';
      if (iface.bridge) return 'bridge';
      if (iface.sriov) return 'sriov';
      return 'masquerade';
    },

    addNetwork() {
      const configs = [...this.networkConfigs];
      configs.push({
        name: 'default',
        networkType: 'pod',
        networkName: '',
        interfaceType: 'masquerade',
        macAddress: '',
      });
      this.networkConfigs = configs;
    },

    removeNetwork(index) {
      const configs = [...this.networkConfigs];
      configs.splice(index, 1);
      this.networkConfigs = configs;
    },
  },

  created() {
    // Ensure at least one default network exists
    if (this.networkConfigs.length === 0) {
      this.addNetwork();
    }
  },
};
</script>

<template>
  <div>
    <div class="row mb-20">
      <div class="col span-12">
        <h3>{{ t('kubevirt.edit.vm.fields.networks.title') }}</h3>
        <p class="text-muted">{{ t('kubevirt.edit.vm.fields.networks.description') }}</p>
      </div>
    </div>

    <div v-for="(network, index) in networkConfigs" :key="`network-${index}`" class="network-config mb-20">
      <div class="row mb-10">
        <div class="col span-12">
          <h4>{{ t('kubevirt.edit.vm.fields.networks.networkTitle', { index: index + 1 }) }}</h4>
        </div>
      </div>
      
      <div class="row mb-10">
        <div class="col span-6">
          <LabeledInput
            v-model:value="network.name"
            :mode="mode"
            :label="t('kubevirt.edit.vm.fields.networks.name.label')"
            :placeholder="t('kubevirt.edit.vm.fields.networks.name.placeholder')"
          />
        </div>
        <div class="col span-6">
          <LabeledSelect
            v-model:value="network.networkType"
            :mode="mode"
            :options="networkTypes"
            :label="t('kubevirt.edit.vm.fields.networks.type.label')"
          />
        </div>
      </div>

      <div v-if="network.networkType === 'multus'" class="row mb-10">
        <div class="col span-6">
          <LabeledInput
            v-model:value="network.networkName"
            :mode="mode"
            :label="t('kubevirt.edit.vm.fields.networks.multusName.label')"
            :placeholder="t('kubevirt.edit.vm.fields.networks.multusName.placeholder')"
          />
        </div>
      </div>

      <div class="row mb-10">
        <div class="col span-6">
          <LabeledSelect
            v-model:value="network.interfaceType"
            :mode="mode"
            :options="interfaceTypes"
            :label="t('kubevirt.edit.vm.fields.networks.interfaceType.label')"
          />
        </div>
        <div class="col span-6">
          <LabeledInput
            v-model:value="network.macAddress"
            :mode="mode"
            :label="t('kubevirt.edit.vm.fields.networks.macAddress.label')"
            :placeholder="t('kubevirt.edit.vm.fields.networks.macAddress.placeholder')"
          />
        </div>
      </div>

      <div class="row mb-10">
        <div class="col span-12">
          <button
            v-if="!isView && networkConfigs.length > 1"
            type="button"
            class="btn btn-sm role-link"
            @click="removeNetwork(index)"
          >
            {{ t('kubevirt.edit.vm.fields.networks.remove') }}
          </button>
        </div>
      </div>

      <hr v-if="index < networkConfigs.length - 1" class="section-divider">
    </div>

    <div class="row">
      <div class="col span-12">
        <button
          v-if="!isView"
          type="button"
          class="btn btn-sm role-tertiary"
          @click="addNetwork"
        >
          {{ t('kubevirt.edit.vm.fields.networks.add') }}
        </button>
      </div>
    </div>
  </div>
</template>

