<script>
import { LabeledInput } from '@rancher/components';
import LabeledSelect from '@shell/components/form/LabeledSelect';
import { get, set } from '@shell/utils/object';

export default {
  name: 'VMBasicConfig',
  components: {
    LabeledInput,
    LabeledSelect,
  },
  
  props: {
    value: {
      type: Object,
      required: true,
    },
    mode: {
      type: String,
      required: true,
    },
  },

  computed: {
    isView() {
      return this.mode === 'view';
    },

    vmLabels: {
      get() {
        const labels = get(this.value, 'metadata.labels') || {};
        // Filter out system labels for editing
        const editableLabels = {};
        Object.entries(labels).forEach(([key, value]) => {
          if (!key.startsWith('kubevirt.io/') && !key.startsWith('cattle.io/')) {
            editableLabels[key] = value;
          }
        });
        return Object.entries(editableLabels).map(([key, value]) => ({ key, value }));
      },
      set(newValue) {
        const currentLabels = get(this.value, 'metadata.labels') || {};
        // Keep system labels
        const systemLabels = {};
        Object.entries(currentLabels).forEach(([key, value]) => {
          if (key.startsWith('kubevirt.io/') || key.startsWith('cattle.io/')) {
            systemLabels[key] = value;
          }
        });
        
        // Add user labels
        const userLabels = {};
        newValue.forEach(({ key, value }) => {
          if (key && value && key.trim() && value.trim()) {
            userLabels[key.trim()] = value.trim();
          }
        });
        
        set(this.value, 'metadata.labels', { ...systemLabels, ...userLabels });
      },
    },

    vmAnnotations: {
      get() {
        const annotations = get(this.value, 'metadata.annotations') || {};
        // Filter out system annotations for editing
        const editableAnnotations = {};
        Object.entries(annotations).forEach(([key, value]) => {
          if (!key.startsWith('kubevirt.io/') && !key.startsWith('cattle.io/') && key !== 'description') {
            editableAnnotations[key] = value;
          }
        });
        return Object.entries(editableAnnotations).map(([key, value]) => ({ key, value }));
      },
      set(newValue) {
        const currentAnnotations = get(this.value, 'metadata.annotations') || {};
        // Keep system annotations and description
        const systemAnnotations = {};
        Object.entries(currentAnnotations).forEach(([key, value]) => {
          if (key.startsWith('kubevirt.io/') || key.startsWith('cattle.io/') || key === 'description') {
            systemAnnotations[key] = value;
          }
        });
        
        // Add user annotations
        const userAnnotations = {};
        newValue.forEach(({ key, value }) => {
          if (key && value && key.trim() && value.trim()) {
            userAnnotations[key.trim()] = value.trim();
          }
        });
        
        set(this.value, 'metadata.annotations', { ...systemAnnotations, ...userAnnotations });
      },
    },

    description: {
      get() {
        return get(this.value, 'metadata.annotations.description') || '';
      },
      set(newValue) {
        if (newValue) {
          set(this.value, 'metadata.annotations.description', newValue);
        } else {
          const annotations = { ...get(this.value, 'metadata.annotations') };
          delete annotations.description;
          set(this.value, 'metadata.annotations', annotations);
        }
      },
    },
  },

  created() {
    this.ensureMetadataStructure();
  },

  methods: {
    ensureMetadataStructure() {
      if (!this.value.metadata) {
        set(this.value, 'metadata', {});
      }
      if (!this.value.metadata.labels) {
        set(this.value, 'metadata.labels', {});
      }
      if (!this.value.metadata.annotations) {
        set(this.value, 'metadata.annotations', {});
      }
    },

    addLabel() {
      this.vmLabels = [...this.vmLabels, { key: '', value: '' }];
    },

    removeLabel(index) {
      const labels = [...this.vmLabels];
      labels.splice(index, 1);
      this.vmLabels = labels;
    },

    addAnnotation() {
      this.vmAnnotations = [...this.vmAnnotations, { key: '', value: '' }];
    },

    removeAnnotation(index) {
      const annotations = [...this.vmAnnotations];
      annotations.splice(index, 1);
      this.vmAnnotations = annotations;
    },
  },
};
</script>

<template>
  <div>
    <div class="row mb-20">
      <div class="col span-12">
        <LabeledInput
          v-model:value="description"
          :mode="mode"
          :label="t('kubevirt.edit.vm.fields.description.label')"
          :placeholder="t('kubevirt.edit.vm.fields.description.placeholder')"
          type="multiline"
          :min-height="60"
        />
      </div>
    </div>

    <div class="row mb-20">
      <div class="col span-12">
        <h3>{{ t('kubevirt.edit.vm.fields.labels.title') }}</h3>
        <div v-for="(label, index) in vmLabels" :key="`label-${index}`" class="row mb-10">
          <div class="col span-5">
            <LabeledInput
              v-model:value="label.key"
              :mode="mode"
              :label="t('kubevirt.edit.vm.fields.labels.key')"
              :placeholder="t('kubevirt.edit.vm.fields.labels.keyPlaceholder')"
            />
          </div>
          <div class="col span-5">
            <LabeledInput
              v-model:value="label.value"
              :mode="mode"
              :label="t('kubevirt.edit.vm.fields.labels.value')"
              :placeholder="t('kubevirt.edit.vm.fields.labels.valuePlaceholder')"
            />
          </div>
          <div class="col span-2">
            <button
              v-if="!isView"
              type="button"
              class="btn btn-sm role-link"
              @click="removeLabel(index)"
            >
              {{ t('generic.remove') }}
            </button>
          </div>
        </div>
        <button
          v-if="!isView"
          type="button"
          class="btn btn-sm role-tertiary"
          @click="addLabel"
        >
          {{ t('kubevirt.edit.vm.fields.labels.add') }}
        </button>
      </div>
    </div>

    <div class="row mb-20">
      <div class="col span-12">
        <h3>{{ t('kubevirt.edit.vm.fields.annotations.title') }}</h3>
        <div v-for="(annotation, index) in vmAnnotations" :key="`annotation-${index}`" class="row mb-10">
          <div class="col span-5">
            <LabeledInput
              v-model:value="annotation.key"
              :mode="mode"
              :label="t('kubevirt.edit.vm.fields.annotations.key')"
              :placeholder="t('kubevirt.edit.vm.fields.annotations.keyPlaceholder')"
            />
          </div>
          <div class="col span-5">
            <LabeledInput
              v-model:value="annotation.value"
              :mode="mode"
              :label="t('kubevirt.edit.vm.fields.annotations.value')"
              :placeholder="t('kubevirt.edit.vm.fields.annotations.valuePlaceholder')"
            />
          </div>
          <div class="col span-2">
            <button
              v-if="!isView"
              type="button"
              class="btn btn-sm role-link"
              @click="removeAnnotation(index)"
            >
              {{ t('generic.remove') }}
            </button>
          </div>
        </div>
        <button
          v-if="!isView"
          type="button"
          class="btn btn-sm role-tertiary"
          @click="addAnnotation"
        >
          {{ t('kubevirt.edit.vm.fields.annotations.add') }}
        </button>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
h3 {
  margin-bottom: 10px;
}
</style>