<script>
import { LabeledInput } from '@rancher/components';
import LabeledSelect from '@shell/components/form/LabeledSelect';
import { get, set } from '@shell/utils/object';

export default {
  name: 'VMResourcesConfig',
  components: {
    LabeledInput,
    LabeledSelect,
  },
  props: {
    value: {
      type: Object,
      required: true,
    },
    mode: {
      type: String,
      required: true,
    },
    rules: {
      type: Function,
      default: () => [],
    },
  },

  data() {
    return {
      memoryUnits: [
        { label: 'Mi', value: 'Mi' },
        { label: 'Gi', value: 'Gi' },
        { label: 'Ti', value: 'Ti' },
      ],
    };
  },

  computed: {
    cpuCores: {
      get() {
        return get(this.value, 'spec.template.spec.domain.cpu.cores') || 1;
      },
      set(newValue) {
        set(this.value, 'spec.template.spec.domain.cpu.cores', parseInt(newValue));
      },
    },

    memoryValue: {
      get() {
        const memory = get(this.value, 'spec.template.spec.domain.memory.guest') || '1Gi';
        return memory.replace(/[A-Za-z]/g, '');
      },
      set(newValue) {
        const unit = this.memoryUnit;
        set(this.value, 'spec.template.spec.domain.memory.guest', `${newValue}${unit}`);
      },
    },

    memoryUnit: {
      get() {
        const memory = get(this.value, 'spec.template.spec.domain.memory.guest') || '1Gi';
        return memory.replace(/[0-9]/g, '') || 'Gi';
      },
      set(newValue) {
        const value = this.memoryValue;
        set(this.value, 'spec.template.spec.domain.memory.guest', `${value}${newValue}`);
      },
    },

    cpuRequests: {
      get() {
        return get(this.value, 'spec.template.spec.domain.resources.requests.cpu') || '';
      },
      set(newValue) {
        if (newValue) {
          set(this.value, 'spec.template.spec.domain.resources.requests.cpu', newValue);
        } else {
          const resources = { ...get(this.value, 'spec.template.spec.domain.resources') };
          if (resources.requests) {
            delete resources.requests.cpu;
            if (Object.keys(resources.requests).length === 0) {
              delete resources.requests;
            }
          }
          set(this.value, 'spec.template.spec.domain.resources', resources);
        }
      },
    },

    cpuLimits: {
      get() {
        return get(this.value, 'spec.template.spec.domain.resources.limits.cpu') || '';
      },
      set(newValue) {
        if (newValue) {
          set(this.value, 'spec.template.spec.domain.resources.limits.cpu', newValue);
        } else {
          const resources = { ...get(this.value, 'spec.template.spec.domain.resources') };
          if (resources.limits) {
            delete resources.limits.cpu;
            if (Object.keys(resources.limits).length === 0) {
              delete resources.limits;
            }
          }
          set(this.value, 'spec.template.spec.domain.resources', resources);
        }
      },
    },

    memoryRequests: {
      get() {
        return get(this.value, 'spec.template.spec.domain.resources.requests.memory') || '';
      },
      set(newValue) {
        if (newValue) {
          set(this.value, 'spec.template.spec.domain.resources.requests.memory', newValue);
        } else {
          const resources = { ...get(this.value, 'spec.template.spec.domain.resources') };
          if (resources.requests) {
            delete resources.requests.memory;
            if (Object.keys(resources.requests).length === 0) {
              delete resources.requests;
            }
          }
          set(this.value, 'spec.template.spec.domain.resources', resources);
        }
      },
    },

    memoryLimits: {
      get() {
        return get(this.value, 'spec.template.spec.domain.resources.limits.memory') || '';
      },
      set(newValue) {
        if (newValue) {
          set(this.value, 'spec.template.spec.domain.resources.limits.memory', newValue);
        } else {
          const resources = { ...get(this.value, 'spec.template.spec.domain.resources') };
          if (resources.limits) {
            delete resources.limits.memory;
            if (Object.keys(resources.limits).length === 0) {
              delete resources.limits;
            }
          }
          set(this.value, 'spec.template.spec.domain.resources', resources);
        }
      },
    },
  },
};
</script>

<template>
  <div>
    <div class="row mb-20">
      <div class="col span-12">
        <h3>{{ t('kubevirt.edit.vm.fields.resources.title') }}</h3>
      </div>
    </div>

    <div class="row mb-20">
      <div class="col span-6">
        <LabeledInput
          v-model:value="cpuCores"
          type="number"
          :mode="mode"
          :label="t('kubevirt.edit.vm.fields.cpu.cores.label')"
          :tooltip="t('kubevirt.edit.vm.fields.cpu.cores.tooltip')"
          min="1"
          :rules="rules('spec.template.spec.domain.cpu.cores')"
        />
      </div>
      <div class="col span-3">
        <LabeledInput
          v-model:value="memoryValue"
          type="number"
          :mode="mode"
          :label="t('kubevirt.edit.vm.fields.memory.guest.label')"
          :tooltip="t('kubevirt.edit.vm.fields.memory.guest.tooltip')"
          min="1"
          :rules="rules('spec.template.spec.domain.memory.guest')"
        />
      </div>
      <div class="col span-3">
        <LabeledSelect
          v-model:value="memoryUnit"
          :mode="mode"
          :options="memoryUnits"
          :label="t('kubevirt.edit.vm.fields.memory.unit.label')"
        />
      </div>
    </div>

    <div class="row mb-20">
      <div class="col span-12">
        <h4>{{ t('kubevirt.edit.vm.fields.resources.requests.title') }}</h4>
        <p class="text-muted">{{ t('kubevirt.edit.vm.fields.resources.requests.description') }}</p>
      </div>
    </div>

    <div class="row mb-20">
      <div class="col span-6">
        <LabeledInput
          v-model:value="cpuRequests"
          :mode="mode"
          :label="t('kubevirt.edit.vm.fields.cpu.requests.label')"
          :tooltip="t('kubevirt.edit.vm.fields.cpu.requests.tooltip')"
          :placeholder="t('kubevirt.edit.vm.fields.cpu.requests.placeholder')"
        />
      </div>
      <div class="col span-6">
        <LabeledInput
          v-model:value="memoryRequests"
          :mode="mode"
          :label="t('kubevirt.edit.vm.fields.memory.requests.label')"
          :tooltip="t('kubevirt.edit.vm.fields.memory.requests.tooltip')"
          :placeholder="t('kubevirt.edit.vm.fields.memory.requests.placeholder')"
        />
      </div>
    </div>

    <div class="row mb-20">
      <div class="col span-12">
        <h4>{{ t('kubevirt.edit.vm.fields.resources.limits.title') }}</h4>
        <p class="text-muted">{{ t('kubevirt.edit.vm.fields.resources.limits.description') }}</p>
      </div>
    </div>

    <div class="row mb-20">
      <div class="col span-6">
        <LabeledInput
          v-model:value="cpuLimits"
          :mode="mode"
          :label="t('kubevirt.edit.vm.fields.cpu.limits.label')"
          :tooltip="t('kubevirt.edit.vm.fields.cpu.limits.tooltip')"
          :placeholder="t('kubevirt.edit.vm.fields.cpu.limits.placeholder')"
        />
      </div>
      <div class="col span-6">
        <LabeledInput
          v-model:value="memoryLimits"
          :mode="mode"
          :label="t('kubevirt.edit.vm.fields.memory.limits.label')"
          :tooltip="t('kubevirt.edit.vm.fields.memory.limits.tooltip')"
          :placeholder="t('kubevirt.edit.vm.fields.memory.limits.placeholder')"
        />
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
h3, h4 {
  margin-bottom: 10px;
}

.text-muted {
  color: var(--muted);
  font-size: 0.9em;
  margin-bottom: 15px;
}
</style>